@import '../../../../shared/view.scss';

.break {
    padding-top: 2.0em;
}

.about {
    padding: 2.0em 0;
    border-radius: 3em;
}

.image {
    clip-path: polygon(5% 5%, 100% 0%, 100% 75%, 75% 75%, 75% 100%, 50% 75%, 0% 75%);
    padding-top: 2.0em;
}

@media (max-width: $maxTabletSize) {
    .image {
      clip-path: none !important;
      padding: 2.0em 0;
    }
  }